<div style=" display: flex;justify-content: center;flex-direction: column;"><div class="form__gallery" id="gallery" v-if="field === 'images'"></div><ul class="form__list"><FormMultiItem v-for="item in items" :id="item.id" :key="item.id" v-model:list="items" :field="field"></FormMultiItem></ul><SubBtn v-if="!isSubdivision" v-show="items.length &lt;= itemLimit" @click="addItem" :text="btnText"></SubBtn><SmallBtn v-else @click="addItem" v-show="items.length &lt; itemLimit" :text="btnText"></SmallBtn></div>