<BaseModal v-model:open="isOpen"><h2 class="content__title">{{ window === 'create' ? 'Создание нового товара' : 'Изменение товара' }}</h2><form class="form" id="create" @submit.prevent="sendForm()"><FormFieldset :legend="'Название'"><FormInput v-model.trim="formData.name" :placeholder-text="'Введите название'" :input-type="'text'"></FormInput></FormFieldset><FormFieldset v-for="item in sections" :legend="item.legend"><FormMultiBlock @change="updateInputGroup" v-model="formData[item.section]" :btn-text="item.btnText" :field="item.section"></FormMultiBlock></FormFieldset><PrimeBtn :class="{ 'button--disabled': isSending }" :text="window === 'create' ? 'Создать' : 'Сохранить'"><BaseLoader v-if="isSending"></BaseLoader><BaseMessage v-if="isEmptyError" v-model:open="isEmptyError" :text="FILL_MESSAGE"></BaseMessage><BaseMessage v-if="isSendingFailed" v-model:open="isSendingFailed" :text="CONNECT_MESSAGE"></BaseMessage><BaseMessage v-if="isSendingSuccessfull" v-model:open="isSendingSuccessfull" :text="SUCCESS_MESSAGE" :type="'success'"></BaseMessage></PrimeBtn></form></BaseModal>